import React, { useEffect, useState } from "react";

import {
  Row,
  Col,
  Input,
  Card,
  Pagination,
  Popconfirm,
  Spin,
  Button,
} from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import { Search } from "react-iconly";
import { connect } from "react-redux";
import { FilterOutlined, UploadOutlined, PlusOutlined } from "@ant-design/icons";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import PageTitle from "../../../../layout/components/content/page-title";
import moment from "moment";
import {
  getVehicles,
  vehiclesDelete,
  isModelVisible,
  SetVehiclesParams,
  isDrawerVisible,
  getvehicleByID
} from "../../../../store/Masters/Vehicles/vehiclesSlice";
import AddVehicles from "./addVehicles";
import { isMobile } from "react-device-detect";
import {
  getMiniBrandFamily,
  getMiniModels,
  getMiniVersion,
} from "../../../../store/mini/miniSlice";
import Filter from "./Filters/Filter";

function Vehicles(props) {
  useEffect(() => {
    props.getVehicles({ page: 1, search: "", page_size: 10 });
  }, []);
  const [isActive, setIsActive] = useState(false);
  const columnClass =
    "hp-p1-body hp-text-color-black-50 hp-text-color-dark-0 hp-font-weight-500 ";

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: 70,
      fixed: isMobile ? null : "left",
    },
    {
      title: "Code",
      dataIndex: "code",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: 120,
      fixed: isMobile ? null : "left",
    },
    {
      title: "Date",
      render: (row) => (
        <span className={columnClass}>{moment(row.createdon).format("DD-MM-YYYY")}</span>
      ),
      filterMode: "tree",
      filterSearch: true,
      width: 120,
    },
    {
      title: "Vehicle Number",
      render: (row) => (
        <span className={columnClass}>{row.vehicleno}</span>
      ),
      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: 120,
    },
    {
      title: "Vehicle TR No",
      render: (row) => (
        <span className={columnClass}>{row.vehicletrno}</span>
      ),
      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: 120,
    },
    {
      title: "Brand",
      render: (row) => (
        <span className={columnClass}>{row.version?.brand?.name}</span>
      ),
      filterMode: "tree",
      filterSearch: true,
      width: 180,
    },
    {
      title: "Brand Family",
      render: (row) => (
        <span className={columnClass}>{row.version?.brandfamily?.name}</span>
      ),
      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: 120,
    },
    {
      title: "Model",
      render: (row) => (
        <span className={columnClass}>{row.version?.model?.name}</span>
      ),
      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: 80,
    },
    {
      title: "Version",
      render: (row) => <span className={columnClass}>{row.version?.name}</span>,
      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: 180,
    },
    {
      title: "Year",
      render: (row) => (
        <span className={columnClass}>{row.year}</span>
      ),
      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.year.length - b.year.length,
      width: 70,
    },
    {
      title: "Engine Number",
      render: (row) => (
        <span className={columnClass}>{row.engineno}</span>
      ),
      filterMode: "tree",
      filterSearch: true,
      width: 210,
    },
    {
      title: "Chassis Number",
      render: (row) => (
        <span className={columnClass}>{row.chassisno}</span>
      ),
      filterMode: "tree",
      filterSearch: true,
      width: 200,
    },
    {
      title: "Fuel Type",
      render: (row) => (
        <span className={columnClass}>{row.version?.fueltype_name}</span>
      ),
      filterMode: "tree",
      filterSearch: true,
      width: 100,
    },
    {
      title: "Priority",
      render: (row) => (
        <span className={columnClass}>{row.version?.priority?.name}</span>
      ),
      filterMode: "tree",
      filterSearch: true,
      width: 100,
    },
    {
      title: "Actions",
      key: "action",
      width: 100,
      fixed: isMobile ? null : "right",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_vehicle") !== -1 && (
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.getvehicleByID(row.id)
            }}
          ></i>
          )}

        {props.userAccessList?.indexOf("Masters.delete_vehicle") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.vehiclesDelete(row.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
          )}
        </Space>
      ),
    },
  ];

  const data = props.vehicleslist.map((row, key) => ({
    ...row,
    index:
      (props.vehiclesParams.page - 1) * props.vehiclesParams.page_size +
      key +
      1,
  }));

  function onPageChange(page, pageSize) {
    props.SetVehiclesParams({
      ...props.vehiclesParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getVehicles({
      ...props.vehiclesParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetVehiclesParams({ ...props.vehiclesParams, page: 1, search: val });
    props.getVehicles({ ...props.vehiclesParams, page: 1, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  const { Search } = Input;
  return (
    <Row gutter={[12, 12]} >
      <PageTitle
        pageTitle="Vehicles"
        // addModelTile={
        //   props.userAccessList?.indexOf("Masters.add_vehicle") !== -1 &&
        //   "Add Vehicle"
        // }
        // goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Row  gutter={[12, 12]}>
          <Col span={24} xs={24} sm={24} md={14}>
            <Search
              placeholder="Search..."
              allowClear
              onChange={(e) => {
                if (e.target.value == "") {
                  props.getVehicles({
                    ...props.vehiclesParams,
                    page: 1,
                    search: "",
                  });
                  props.SetVehiclesParams({
                    ...props.vehiclesParams,
                    page: 1,
                    search: "",
                  });
                }
              }}
              onSearch={(value) => {
                onSearch(value);
              }}
            />
          </Col>

          <Col span={24} xs={8} sm={8} md={3}>
            <Button
              onClick={() => {
                props.isDrawerVisible(true);
                // setIsActive((current) => !current);
              }}
              className="hp-w-100 hp-p-12"
              icon={<FilterOutlined className="hp-mr-10" />}
              // style={{
              //   backgroundColor: isActive ? "#0010f7" : "",
              //   color: isActive ? "#fff" : "",
              // }}
            >
              {!isMobile && "Filter"}
            </Button>
          </Col>

          {props.userAccessList?.indexOf("Masters.add_vehicle") !== -1 && (
            <Col span={24} xs={8} sm={8} md={4}>
              <Button
                onClick={() => {
                  props.isModelVisible(true);
                }}
                className="hp-w-100 hp-p-12"
                icon={<PlusOutlined className="hp-mr-10" />}
              >
                {!isMobile && "Add Vehicles"}
              </Button>
            </Col>
          )}
        </Row>
      </Col>

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 1300 }}
                  pagination={false}
                  size="middle"
                  className="hp-p-0"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.vehiclesParams?.page}
                pageSize={props.vehiclesParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddVehicles />
      <Filter />
    </Row>
  );
}

function mapAreaToProps({ vehicles, system }) {
  return {
    vehicleslist: vehicles.vehicleslist,
    noofpages: vehicles.noofpages,
    listCount: vehicles.listCount,
    vehiclesParams: vehicles.vehiclesParams,
    loading: vehicles.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapAreaToProps, {
  getVehicles,
  vehiclesDelete,
  isModelVisible,
  SetVehiclesParams,
  getMiniBrandFamily,
  getMiniModels,
  getMiniVersion,
  isDrawerVisible,
  getvehicleByID
})(Vehicles);
