import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  Row,
  Col,
  Divider,
  Form,
  Input,
  Button,
  Drawer,
  Space,
  Select,
  DatePicker,
} from "antd";
import moment, { min } from "moment";

import {
  isDrawerVisible,
  FilterInputChangeValue,
  getVehicles,
  SetVehiclesParams,
} from "../../../../../store/Masters/Vehicles/vehiclesSlice";
import {
  getMiniBrands,
  getMiniBrandFamily,
  getMiniPriority,
  getMiniModels,
  getMiniVehicletypes,
  getMiniBrandsClearData,
  getMiniBrandFamilyClearData,
  getMiniPriorityClearData,
  getMiniModelsClearData,
  getMiniVehicletypesClearData,
} from "../../../../../store/mini/miniSlice";

import {
  getVersionfeatures,
  SetVersionfeaturesParams,
} from "../../../../../store/Masters/Versionfeatures/versionfeaturesSlice";

var date = new Date(),
  y = date.getFullYear(),
  m = date.getMonth();

const filter = (props) => {
  const [form] = Form.useForm();
  useEffect(() => {
    props.getMiniBrands();
    props.getMiniBrandFamily();
    props.getMiniModels();
    props.getMiniVehicletypes();
    props.getVersionfeatures({ ...props.versionfeaturesParams });
    props.getMiniPriority();
  }, []);

  const FUELTYPE_CHOICES = [
    { id: 1, name: "Petrol" },
    { id: 2, name: "Diesel" },
    { id: 3, name: "Cng" },
    { id: 4, name: "Electric" },
    { id: 5, name: "Bio diesel" },
  ];

  const onFinish = () => {
    props.SetVehiclesParams({
      ...props.vehiclesParams,
      start_date: props.vehiclesParams.start_date,
      end_date: props.vehiclesParams.end_date,
      page: 1,
      search: "",
      page_size: 10,
    });

    props.getVehicles({
      ...props.vehiclesParams,
      // start_date:
      //   props.vehiclesParams.start_date != null &&
      //   props.vehiclesParams.start_date != ""
      //     ? moment(props.vehiclesParams.start_date).format("YYYY-MM-DD")
      //     : "",
      // end_date:
      //   props.vehiclesParams.end_date != null &&
      //   props.vehiclesParams.end_date != ""
      //     ? moment(props.vehiclesParams.end_date)
      //         .add(1, "days")
      //         .format("YYYY-MM-DD")
      //     : "",
      start_date: props.vehiclesParams.start_date,
      end_date: props.vehiclesParams.end_date,
      page: 1,
      search: "",
      page_size: 10,
    });
    props.isDrawerVisible(false);
  };

  function clearFilter() {
    props.SetVehiclesParams({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });

    props.getVehicles({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });

    props.getMiniBrands();
    props.getMiniBrandFamily();
    props.getMiniModels();
    props.getMiniVehicletypes();
    props.getVersionfeatures({ ...props.versionfeaturesParams });
    props.getMiniPriority();
  }
  function disableDateRanges(range = { startDate: false, endDate: false }) {
    const { startDate, endDate } = range;
    return function disabledDate(current) {
      let startCheck = true;
      let endCheck = true;
      if (startDate) {
        startCheck =
          current &&
          current < moment(startDate + " 00:00:01", "YYYY-MM-DD HH:mm:ss");
      }
      if (endDate) {
        endCheck =
          current &&
          current >
            moment(
              moment(endDate).format("YYYY-MM-DD") + " 23:59:59",
              "YYYY-MM-DD HH:mm:ss"
            );
      }

      return (startDate && startCheck) || (endDate && endCheck);
    };
  }

  useEffect(() => {
    form.setFieldsValue({
      start_date: props.vehiclesParams?.start_date,
      end_date: props.vehiclesParams?.end_date,
      version__brand: props.vehiclesParams.version__brand,
      version__brandfamily: props.vehiclesParams.version__brandfamily,
      version__model: props.vehiclesParams.version__model,
      version__vehicletype: props.vehiclesParams.version__vehicletype,
      version__priority: props.vehiclesParams.version__priority,
      version__fueltype: props.vehiclesParams.version__fueltype,
    });
  }, [props.vehiclesParams]);

  function onSearch(val) {
    props.getVehicles({ page: 1, search: val, page_size: 10 });
  }
  return (
    <Row gutter={[24, 24]}>
      <Col span={24}>
        <Drawer
          title="Filters"
          width={420}
          className="hp-drawer-mobile"
          onClose={() => {
            props.isDrawerVisible(false);
          }}
          placement="right"
          visible={props.drawer}
          bodyStyle={{
            paddingBottom: 10,
          }}
        >
          <Form
            form={form}
            layout="vertical"
            name="basic"
            initialValues={{ remember: true }}
            onFinish={onFinish}
          >
            <Row gutter={[12, 12]}>
              <Col span={12}>
                <Form.Item label="From Date" name="start_date">
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: new Date(),
                    })}
                    onChange={(date) => {
                      props.FilterInputChangeValue({
                        key: "start_date",
                        value: date == null ? "" : date,
                      });
                      if (date == null) {
                        props.FilterInputChangeValue({
                          key: "end_date",
                          value: "",
                        });
                      }
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="To Date" name="end_date">
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: moment().format("YYYY-MM-DD"),
                      startDate: moment(
                        props.vehiclesParams?.start_date
                      ).format("YYYY-MM-DD"),
                    })}
                    disabled={
                      props.vehiclesParams.start_date == null ||
                      props.vehiclesParams.start_date == ""
                        ? true
                        : false
                    }
                    onChange={(date) => {
                      props.FilterInputChangeValue({
                        key: "end_date",
                        value: date == null ? "" : date,
                      });
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="Brand :" name="version__brand">
                  <Select
                    showSearch
                    allowClear
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    placeholder="Select  Brand"
                    optionFilterProp="Brand"
                    onChange={(value) => {
                      props.getMiniBrandFamily({ brand: value });
                      props.getMiniBrandFamilyClearData,
                        props.getMiniModelsClearData,
                        props.FilterInputChangeValue({
                          key: "version__brand",
                          value: value,
                        });
                    }}
                    value={
                      props.vehiclesParams.brand_id == null
                        ? ""
                        : props.vehiclesParams?.brand_id
                    }
                  >
                    {props.miniBrandsList.map((brand, index) => {
                      return (
                        <Option key={index} value={brand.id}>
                          {brand.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="Brand Family :" name="version__brandfamily">
                  <Select
                    showSearch
                    allowClear
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    placeholder="Select Brand Family"
                    optionFilterProp="brand Family"
                    onChange={(value) => {
                      props.getMiniModels({ brandfamily: value });
                      props.getMiniModelsClearData,
                        props.FilterInputChangeValue({
                          key: "version__brandfamily",
                          value: value,
                        });
                    }}
                    value={
                      props.vehiclesParams.brandfamily_id == null
                        ? ""
                        : props.vehiclesParams?.brandfamily_id
                    }
                  >
                    {props.miniBrandFamilyList.map((brandfamily, index) => {
                      return (
                        <Option key={index} value={brandfamily.id}>
                          {brandfamily.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="Model :" name="version__model">
                  <Select
                    showSearch
                    allowClear
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    placeholder="Select Model"
                    optionFilterProp="Model"
                    onChange={(value) => {
                      props.FilterInputChangeValue({
                        key: "version__model",
                        value: value,
                      });
                    }}
                    value={
                      props.vehiclesParams.model_id == null
                        ? ""
                        : props.vehiclesParams?.model_id
                    }
                  >
                    {props.miniModelsList.map((model, index) => {
                      return (
                        <Option key={index} value={model.id}>
                          {model.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="Vehicle Type :" name="version__vehicletype">
                  <Select
                    showSearch
                    allowClear
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    placeholder="Select Vehicle Type"
                    optionFilterProp="vehicletype"
                    onChange={(value) => {
                      props.FilterInputChangeValue({
                        key: "version__vehicletype",
                        value: value,
                      });
                    }}
                    value={
                      props.vehiclesParams.vehicletype_id == null
                        ? ""
                        : props.vehiclesParams?.vehicletype_id
                    }
                  >
                    {props.miniVehicletypesList.map((vehicletype, index) => {
                      return (
                        <Option key={index} value={vehicletype.id}>
                          {vehicletype.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="Priority :" name="version__priority">
                  <Select
                    showSearch
                    allowClear
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    placeholder="Select Priority"
                    optionFilterProp="priority"
                    onChange={(value) => {
                      props.FilterInputChangeValue({
                        key: "version__priority",
                        value: value,
                      });
                    }}
                    value={
                      props.vehiclesParams.priority_id == null
                        ? ""
                        : props.vehiclesParams?.priority_id
                    }
                  >
                    {props.miniPriority.map((priority, index) => {
                      return (
                        <Option key={index} value={priority.id}>
                          {priority.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="Fuel Type" name="version__fueltype">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select Fuel Type"
                    optionFilterProp="fueltype"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.FilterInputChangeValue({
                        key: "version__fueltype",
                        value: value,
                      });
                    }}
                    value={
                      props.vehiclesParams.fueltype == null
                        ? ""
                        : props.vehiclesParams?.fueltype
                    }
                  >
                    {FUELTYPE_CHOICES.map((fueltype, index) => {
                      return (
                        <Option key={index} value={fueltype.id}>
                          {fueltype.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={[12, 12]}>
              <Col md={12}>
                <Button
                  block
                  type="secondary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    clearFilter();
                  }}
                  htmlType="button"
                >
                  Clear
                </Button>
              </Col>
              <Col md={12}>
                <Button
                  block
                  type="primary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    form.submit();
                  }}
                >
                  Apply
                </Button>
              </Col>
            </Row>
          </Form>
        </Drawer>
      </Col>
    </Row>
  );
};

function mapStateToProps({ vehicles, mini, versionfeatures }) {
  return {
    drawer: vehicles.drawer,
    vehiclesParams: vehicles.vehiclesParams,
    miniBrandsList: mini.miniBrandsList,
    miniBrandFamilyList: mini.miniBrandFamilyList,
    miniPriority: mini.miniPriority,
    miniModelsList: mini.miniModelsList,
    miniVehicletypesList: mini.miniVehicletypesList,

    versionfeaturesParams: versionfeatures.versionfeaturesParams,
    versionfeatureslist: versionfeatures.versionfeatureslist,
  };
}

export default connect(mapStateToProps, {
  isDrawerVisible,
  SetVehiclesParams,
  FilterInputChangeValue,
  getVehicles,
  getMiniBrands,
  getMiniBrandFamily,
  getMiniPriority,
  getMiniModels,
  getMiniVehicletypes,
  getVersionfeatures,
  SetVersionfeaturesParams,

  getMiniBrandsClearData,
  getMiniBrandFamilyClearData,
  getMiniPriorityClearData,
  getMiniModelsClearData,
  getMiniVehicletypesClearData,
})(filter);
